/*------------ Thumb Offer ----------------*/
.thumb_offer{
    padding: $xxl-size $pd-xsmall;
}

.thumb_offer .thumb_offer__item{
    padding: 0 .9rem; 
    margin-bottom: 3rem;
}

.thumb_item__title h4{
    font-weight: 700 ;
    margin-bottom: .8rem;
    font-size: 1.2rem !important;
    letter-spacing: 0.01rem;
    line-height: 1.5;
    color: lighten($secondary-color, 10%);
    text-transform: uppercase;
    transition: all .2s;

}

.thumb_offer .main_offer_sold .title_item,
.thumb_offer .saving_before_price,
.thumb_offer .main_offer_price .saving_price{
    font-size: 1rem !important;
}


.thumb_offer .main_offer_price{
    text-align: right;
}

.card{
    cursor: pointer;
    background: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0 4px 9px 0 rgba(30,59,91,0.1);
    box-shadow: 0 4px 9px 0 rgba(30,59,91,0.1);
    backface-visibility: hidden; // fixing a bug in chrome that move the element on hover

    &:hover > .card-image img{
        transform: scale(1.1);
    } 

    // &:hover > .card-content .thumb_item__title h4{
    //     color: $primary-color;
    // }
}



.card-content {
    padding: 1.688rem !important;
    //padding: .8rem 0 0 0 ;
}

.card-image{
    position: relative;
    overflow: hidden;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    
    img{
        transition: all .2s;
    }
}

.overlay{
    position: absolute;
    display: block;
    z-index: 1;
    color: white;
    text-align: center;
    left: 10px;
    top: 10px;

    &_content{
        border: 1px solid $primary-color;
        border-radius: 50px;
        font-size: .9rem;
        height: 30px;
        width: 30px;
        line-height: 30px;
    }
}


