/*------------ Base ----------------*/

html {
    font-size: 62.5%;
}

body {
    font-family: Helvetica, Arial, sans-serif;
    font-size: $m-size; 
}

button { 
    cursor: pointer;
   
}

.button{
    transition: all .2s;
}

.button_secondary{
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 0.003rem;
    background-color: $secondary-color !important; 
}


.buttom_icon {
    margin-left: .7rem;
    font-size: 1rem;
    transform: translateX(0);
    transition: all .2s;
}

.button_secondary:hover > .buttom_icon {
    transform: translateX(15px);
}

.button_secondary:hover{
    background-color: $primary-color !important; 
}

.button_primary:hover{
    background-color: $secondary-color;
}

button:disabled {
    cursor: default;
}

.input, .textare{
    box-shadow: none;
}

.input{
    font-size: .8rem !important;
    padding: 1.05rem .6rem !important;
}

.full-width{
    width: 100%;
}

// Background

// With bottom border

.section_bg{
    background: #f7f8fc99;
    border-bottom: 1px solid darken($light-grey, 2%);
}

// Logo
.logo_main,
.logo_line{
    font-family: $font-main;
    text-align: center;
    line-height: 0.9rem;
}

.logo_main{
    color: $primary-color;
    font-size: 1.9rem;
}

.logo_line{
    color: $brown-color;
    display: block;
    font-size: 1.35rem
}







