/*------------ Settings ----------------*/

// Colors

$primary-color: #fab232;
$brown-color: #583917;
$primary-color-dark:  darken($primary-color, 10%); 
$lighter: #a09ea2;
$secondary-color: #3a3735;
$light-grey: #F5F5F5;
$light-m-grey: rgb(228, 228, 228);

// Spacing

$s-size: 1.2rem;
$m-size: 1.6rem;
$l-size: 2.2rem;
$xl-size: 4.8rem;
$xxl-size: 7.8rem;

// Paddings

$pd-xsmall: 1rem;
$pd-small: 2rem;
$pd-medium: 4rem;
$pd-large: 6rem;
$pd-xl: 8rem;

$desktop-breakpoint: 45rem;


