@import './base/fonts';
@import './base/settings';
@import './base/base';

// 1. Import the initial variables from Bulma
@import "node_modules/bulma/sass/utilities/initial-variables";
@import "node_modules/bulma/sass/utilities/functions";

// 2. Set your own initial variables
$none: none;

// 3. Set the derived variables BULMA

// font
$family-primary: $font-secondary;

// color
$primary: $primary-color;

// Inputs
$input-shadow: $none;
$input-focus-box-shadow-size: $none;
$input-focus-border-color: $primary;
$input-hover-border-color: $none;

// Buttons
$button-color: white;
$button-border-color: $none;
$button-background-color: $primary;

$button-hover-border-color: $none;
$button-hover-color: white;

$button-active-color: white;
$button-active-border-color: $none;

// Menu
$navbar-dropdown-arrow: $primary;
$navbar-item-hover-color: $secondary-color;

.navbar-burger span{
    height: 2px !important;
}

// Slider
$carousel-title-size: $size-2;

// 4. Setup your Custom Colors
// 5. Add new color variables to the color map.
// @import "../sass/utilities/derived-variables";

@import 'node_modules/bulma/bulma';
@import './components/thumbsOffer';


// general bulma
.navbar{
    padding: 1rem 0;
    font-size: .9rem;
    letter-spacing: 0.019rem;
}

.navbar.has-shadow {
    box-shadow: none;
    border-bottom: 1px solid lighten($secondary-color, 73%);
}

.button{
    font-size: .9rem;
    padding: 1.3rem;
}

p{  
    font-size: 1.1rem;
    line-height: 1.7rem;
    letter-spacing: 0.019rem;
}

@import './components/menu';
@import './components/mainOffer';
@import './components/footer';
@import './components/copyright';





