/*------------ Main Offer  and common Thumb Offer----------------*/

.main_offer{
    padding: $xxl-size 1rem;
}


h6{
    text-transform: uppercase;

    i{
        margin-right: .5rem;
        font-size: 1.4rem;
        vertical-align: inherit;  
    }
}

.main_offer_title h5{
    font-weight: 700;
    margin-bottom: 1.1rem;
    text-transform: uppercase;
    font-size: 1.6rem;
    letter-spacing: 0.01rem;
}

#main_offer .main_offer_saving, 
#main_offer .main_offer_sold{
    padding-bottom: .5rem;
}

.main_offer .main_offer_due_time h6, 
.main_offer .main_offer_saving h6{
    vertical-align: sub;
    letter-spacing: 0.019rem;
}

// -webkit-box-shadow: 0 4px 9px 0 rgba(30, 59, 91, 0.1);
// box-shadow: 0 4px 9px 0 rgba(30, 59, 91, 0.1);
// }

.main_offer_due_time h6, 
.saving_price{
    color: $primary-color-dark;
}

.title_item{
    color: lighten($secondary-color, 10%);
    vertical-align: text-bottom;
    font-weight: 600;
    line-height: 1.125;
    font-size: 1.2rem;
    letter-spacing: 0.019rem;

    i{
        font-size: 1.2rem;
    }
}

.main_offer_price{

    .saving_before_price{
        text-decoration:line-through;
        margin-right: .4rem;
        color: lighten($secondary-color, 10%);
    }

    .saving_price{
        font-size: 1.4rem;
        font-weight: 800;
    }
}
.main_offer_small_description{
    margin: .9rem 0;
}

// Carousel
.carousel{
    backface-visibility: hidden; // fixing a bug in chrome that move the element on hover
    -webkit-backface-visibility: hidden;
    border-radius: 5px;
    -webkit-border-radius: 5px;
}

.carousel-nav-right, .carousel-nav-left{
    background-color: transparent !important;
    border: 1px solid #fab232;
    color: white !important;
    border-radius: 22px;
    margin: 0 1rem;
    padding: 18px !important;
}


