/*------------ Footer ----------------*/

.main_footer{
    border-top: 1px solid $light-grey;
    border-bottom: 1px solid $light-grey;
    padding: $xl-size $pd-xsmall;
    
    .title{
        color: $secondary-color;
        font-size: 0.9rem;
    }
    
    .footer_links li{
        display: inline-block;
        padding-right: 2rem;
    }

    .footer_links a{
        transition: all .2s;
        color: $lighter;
        font-size: .9rem;
    }

    .footer_links a:hover{
        color: darken($secondary-color, 90%)
    }

    .button .icon{
        font-size: .7rem;
    }

}

.social-wraper{
    text-align: right;
    
    .social{
        & .icon{
            color: $lighter;
            border-radius: 100%;
            border: 1px solid $lighter;
            margin: 0 .2rem;
            transition: all .3s;

            &:hover{
                color:white;
                border-color:$primary-color;;
                background-color: $primary-color;
            }
        }
    }
}
 


